import React from 'react'
import './App.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import SA from './Images/SA.jpg'
import CR from './Images/CR.jpg'
import PR from './Images/PR.jpg'
import RE from './Images/RE.jpg'
import VS from './Images/VS.jpg'
import Background from './background'
 

function Projectss() {
    return(
    <div class=" pos row row-cols-1 row-cols-md-5 ">
    <div class="col mb-5 ">
      <div class="card h-90 ">
        <img src={SA} class="card-img-top" alt="..."></img>
        <div class="card-body">
          <h5 class="card-title">Application Sentiment Analysis based on Review Data</h5>
          <p class="card-text">The aim of the project is to make Android Users aware of the applications which possess as a
          threat to the User’s devices and the information contained in it.</p>
        </div>
      </div>
    </div>
    <div class="col mb-4">
      <div class="card h-90">
        <img src={CR} class="card-img-top" alt="..."></img>
        <div class="card-body">
          <h5 class="card-title">“CONG RUSH” (2D Platformer Game)</h5>
          <p class="card-text">2D platformer game which interacts through the top chartbuster song in every level.</p>
        </div>
      </div>
    </div>
    <div class="col mb-4">
      <div class="card h-90">
        <img src={PR} class="card-img-top" alt="..."></img>
        <div class="card-body">
          <h5 class="card-title">Optimization of 1 million particle system</h5>
          <p class="card-text">Optimization of 1 million particle system using various optimization techniques.</p>
        </div>
      </div>
    </div>
    <div class="col mb-4">
      <div class="card h-90">
        <img src={RE} class="card-img-top" alt="..."></img>
        <div class="card-body">
          <h5 class="card-title">Recycle Application for Chicago</h5>
          <p class="card-text">Developed an application for google that informs users about the location to recycle various
          types’ plastics by scanning QR code.</p>
        </div>
      </div>
    </div>
    <div class="col mb-4">
      <div class="card h-90">
        <img src={VS} class="card-img-top" alt="..."></img>
        <div class="card-body">
          <h5 class="card-title">Video Rental Shop</h5>
          <p class="card-text"> Manage A Video Shop like you can insert the videos then you can delete the videos and  so on..</p>
        </div>
      </div>
    </div>
  </div>
    )

}
   




class Projects extends React.Component {
    render() {

        return (
            <div className>
            <Background/>
                < Projectss />
            </div>
            
        )
    }


}

export default Projects;